import { useState, useEffect } from 'react';
import React from 'react';
import './Dashboard.css';
import {
    useHistory
} from "react-router-dom";

function Dashboard(){
    const [userdata, setUserdata] = useState([]);
    let history = useHistory()
    useEffect(() => {
        // Update the document title using the browser API
        let data = localStorage.getItem("userdata")
        console.log(JSON.parse(data));
        let parseUserData = JSON.parse(data);
        console.log(parseUserData);
        if(typeof parseUserData==='undefined' || parseUserData===null || parseUserData==='null'){
            history.push("/");
        }
        setUserdata(parseUserData);

    }, []);

    const logoutuser = () => {
        localStorage.removeItem("userdata");
        history.push("/");
    }
    return (
        <div className="page-content page-container" id="page-content">
    <div className="padding">
        <div className="row container d-flex justify-content-center">
            <div className="col-xl-6 col-md-12">
                <div className="card user-card-full">
                    <div className="row m-l-0 m-r-0">
                        <div className="col-sm-4 bg-c-lite-green user-profile">
                            <div className="card-block text-center text-white">
                                <div className="m-b-25"> <img src="https://img.icons8.com/bubbles/100/000000/user.png" className="img-radius" alt="User-Profile-Image"/> </div>
                                <h6 className="f-w-600">{userdata.username}</h6>
                                 <i className=" mdi mdi-square-edit-outline feather icon-edit m-t-10 f-16"></i>
                            </div>
                        </div>
                        <div className="col-sm-8">
                            <div className="card-block">
                                <h6 className="m-b-20 p-b-5 b-b-default f-w-600">Information</h6>
                                <div className="row">
                                    <div className="col-sm-6">
                                        <p className="m-b-10 f-w-600">Age</p>
                                        <h6 className="text-muted f-w-400">{userdata.age}</h6>
                                    </div>
                                    <div className="col-sm-6">
                                        <p className="m-b-10 f-w-600">Score</p>
                                        <h6 className="text-muted f-w-400">{userdata.score}</h6>
                                    </div>
                                </div>
                                <h6 className="m-b-20 m-t-40 p-b-5 b-b-default f-w-600">Projects</h6>
                                <div className="row">
                                    <div className="col-sm-12">
                                    <button type="button" className="btn btn-danger" onClick={()=>logoutuser()}>Logout</button>
                                    </div>
                                   
                                </div>
                                
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
    )
}

export default Dashboard;