import { useState, useEffect } from 'react';
import React from 'react';
import './Login.css';
import {
    useHistory
} from "react-router-dom";

import Logosvg from '../../assets/logo.svg'
import Loginimg from '../../assets/login 2.jpg'
import {API_URL,LOGIN_API} from '../../config/index';
import axios from 'axios';
function Login(){


  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  
  let history = useHistory()

  
    useEffect(() => {
        // Update the document title using the browser API
        let data = localStorage.getItem("userdata")
        console.log(JSON.parse(data));
        let parseUserData = JSON.parse(data);
        console.log(parseUserData);
        console.log(typeof parseUserData);
       
        if(parseUserData && typeof parseUserData!='undefined'){
          history.push("/dashboard");
        }

    }, []);

  const loginuser = () => {
  

    // With all properties
    let body = {
      username: username,
      password: password
    };
    axios
      .post(API_URL+LOGIN_API, body)
      .then(function(response) {
        console.log(response.data);
        if(response.data.status===200){
          localStorage.setItem("userdata", JSON.stringify(response.data.data[0]));
          history.push("/dashboard");
        }
      })
      .catch(function(error) {
        console.log(error);
      });
    
  }
  
    return (
    <div className="container">
      <div className="card login-card">
        <div className="row no-gutters">
          <div className="col-md-5">
            <img src={Loginimg} alt="login" className="login-card-img" />
          </div>
          <div className="col-md-7">
            <div className="card-body">
              <div className="brand-wrapper">
                <img src={Logosvg} alt="logo" className="logo" />
              </div>
              <p className="login-card-description">Sign into your account</p>
              <form action="#!">
                  <div className="form-group">
                    <label htmlFor="username" className="sr-only">Username</label>
                    <input type="text" name="username" id="username" className="form-control" placeholder="Enter Username"  required onChange={(e) => setUsername(e.target.value)}/>
                  </div>
                  <div className="form-group mb-4">
                    <label htmlFor="password" className="sr-only">Password</label>
                    <input type="password" name="password" id="password" className="form-control" placeholder="***********"  required onChange={(e) => setPassword(e.target.value)}/>
                  </div>
                  <input name="login" id="login" className="btn btn-block login-btn mb-4" type="button" value="Login"  onClick={() => loginuser()} />
                </form>
                <a href="#!" className="forgot-password-link">Forgot password?</a>
                <p className="login-card-footer-text">Don't have an account? <a href="/register" className="text-reset" >Register here</a></p>
                <nav className="login-card-footer-nav">
                  <a href="#!">Terms of use.</a>
                  <a href="#!">Privacy policy</a>
                </nav>
            </div>
          </div>
        </div>
      </div>
      
    </div>

  
    )
}

export default Login;