import { useState, useEffect } from 'react';
import React from 'react';
import './Register.css';
import {
    useHistory
} from "react-router-dom";

import Logosvg from '../../assets/logo.svg'
import Loginimg from '../../assets/login 2.jpg'
import {API_URL,REGISTER_API} from '../../config/index';
import axios from 'axios';
function Register(){
  

  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [age, setAge] = useState("");
  const [score, setScore] = useState("");
  let history = useHistory()

  const registeruser = () => {
    

    // With all properties
    let body = {
      username: username,
      password: password,
      age: age,
      score: score
    };
    axios
      .post(API_URL+REGISTER_API, body)
      .then(function(response) {
        console.log(response.data);
        if(response.data.status===200){
          alert(response.data.message);
          history.push("/");
        }
      })
      .catch(function(error) {
        console.log(error);
      });
    
  }
  
    return (
    
     
    <div className="container">
      <div className="card login-card">
        <div className="row no-gutters">
          <div className="col-md-5">
            <img src={Loginimg} alt="login" className="login-card-img" />
          </div>
          <div className="col-md-7">
            <div className="card-body">
              <div className="brand-wrapper">
                <img src={Logosvg} alt="logo" className="logo" />
              </div>
              <p className="login-card-description">Register your account</p>
              <form action="#!">
                  <div className="form-group">
                    <label htmlFor="username" className="sr-only">Username</label>
                    <input type="text" name="username" id="username" className="form-control" placeholder="Enter Username" required onChange={(e) => setUsername(e.target.value)}/>
                  </div>
                  <div className="form-group">
                    <label htmlFor="age" className="sr-only">Age</label>
                    <input type="number" name="age" id="age" className="form-control" placeholder="Enter Age" required onChange={(e) => setAge(e.target.value)}/>
                  </div>

                  <div className="form-group">
                    <label htmlFor="score" className="sr-only">Score</label>
                    <input type="number" name="score" id="score" className="form-control" placeholder="Enter score" required onChange={(e) => setScore(e.target.value)}/>
                  </div>


                  <div className="form-group mb-4">
                    <label htmlFor="password" className="sr-only">Password</label>
                    <input type="password" name="password" id="password" className="form-control" placeholder="***********" required  onChange={(e) => setPassword(e.target.value)}/>
                  </div>
                  <input name="login" id="login" className="btn btn-block login-btn mb-4" type="button" value="Register" onClick={() => registeruser()} />
                </form>
                <p className="login-card-footer-text">Already have account? <a href="/" className="text-reset" >Login here</a></p>
                <nav className="login-card-footer-nav">
                  <a href="#!">Terms of use.</a>
                  <a href="#!">Privacy policy</a>
                </nav>
            </div>
          </div>
        </div>
      </div>
      
    </div>

  
    )
}

export default Register;